import {instance} from "./auth-api";
import {AxiosResponse} from "axios";


export const CardsApi = {
    getCards(data: getCardsParamsType) {
        return instance.get<getCardsParamsType, AxiosResponse<ResponseSetNewCards>>(`/cards/card`, {
            params: data
        })
    },
    newCard(card: CreateCardsType) {
        return instance.post('/cards/card', {card})
    },
    deleteCard(_id: string,cardsPack_id:string) {
        return instance.delete(`cards/card?id=${_id}&cardsPack_id=${cardsPack_id}`)
    },
    updatedCard(card: ChangeCardsType) {
        return instance.put('/cards/card', {card})
    }
}

export type ResponseSetNewCards = {
    cards: [
        {
            answer: string,
            question: string,
            cardsPack_id: string,
            grade: number,
            shots: number,
            user_id: string,
            created: string,
            updated: string,
            _id: string,
        },
    ]
    cardsTotalCount: number,
    maxGrade: number,
    minGrade: number,
    page: number,
    pageCount: number,
    packUserId: string,
}

export type getCardsParamsType = {
    cardAnswer?: string, //  
    cardQuestion?: string, //  
    cardsPack_id: string,
    min?: number, //  
    max?: number, //  
    sortCards?: number, //  
    page?: number | undefined, //  
    pageCount?: number | undefined, //  
}

export type CreateCardsType = {
    cardsPack_id: string,
    question?: string, //     
    answer?: string, //     
    grade?: number, // 0..5,  
    shots?: number, //  
    answerImg?: string, //  
    questionImg?: string,//  
    questionVideo?: string, //  
    answerVideo?: string, //  
}

export type ChangeCardsType = {
    _id: string,
    question?: string, //  
    cardsPack_id: string
}